import type { CSSObject } from '@emotion/react';
import type { HTMLAttributes } from 'react';
import React, { useCallback, useMemo } from 'react';

import { useDesignSystemSafexFlags, useDesignSystemTheme } from '..';
import type { DesignSystemEventProviderAnalyticsEventTypes } from '../DesignSystemEventProvider/DesignSystemEventProvider';
import {
  DesignSystemEventProviderComponentTypes,
  useDesignSystemEventComponentCallbacks,
} from '../DesignSystemEventProvider/DesignSystemEventProvider';
import type { AnalyticsEventOptionalProps, DangerousGeneralProps } from '../types';
import { addDebugOutlineIfEnabled } from '../utils/debug';
import { useNotifyOnFirstView } from '../utils/useNotifyOnFirstView';
export interface PreviewCardProps
  extends DangerousGeneralProps,
    Omit<HTMLAttributes<HTMLDivElement>, 'title'>,
    AnalyticsEventOptionalProps<
      DesignSystemEventProviderAnalyticsEventTypes.OnClick | DesignSystemEventProviderAnalyticsEventTypes.OnView
    > {
  icon?: React.ReactNode;
  title?: React.ReactNode;
  subtitle?: React.ReactNode;
  titleActions?: React.ReactNode;
  startActions?: React.ReactNode;
  endActions?: React.ReactNode;
  image?: React.ReactNode;
  fullBleedImage?: boolean;
  size?: 'default' | 'large';
  onClick?: React.MouseEventHandler<HTMLDivElement>;
  disabled?: boolean;
  selected?: boolean;
  href?: string;
  /** If href is defined, this will be used as the target attribute */
  target?: string;
}
export const PreviewCard = ({
  icon,
  title,
  subtitle,
  titleActions,
  children,
  startActions,
  endActions,
  image,
  fullBleedImage = true,
  onClick,
  size = 'default',
  dangerouslyAppendEmotionCSS,
  componentId,
  analyticsEvents = [],
  disabled,
  selected,
  href,
  target,
  ...props
}: PreviewCardProps) => {
  const styles = usePreviewCardStyles({ onClick, size, disabled, fullBleedImage });
  const tabIndex = onClick && !href ? 0 : undefined;
  const role = onClick && !href ? 'button' : undefined;
  const showFooter = startActions || endActions;
  const memoizedAnalyticsEvents = useMemo(() => analyticsEvents, [analyticsEvents]);
  const eventContext = useDesignSystemEventComponentCallbacks({
    componentType: DesignSystemEventProviderComponentTypes.PreviewCard,
    componentId,
    analyticsEvents: memoizedAnalyticsEvents,
  });
  const { elementRef: previewCardRef } = useNotifyOnFirstView<HTMLDivElement>({
    onView: eventContext.onView,
  });
  const onClickWrapper = useCallback(
    (e: React.MouseEvent<HTMLDivElement>) => {
      if (onClick) {
        eventContext.onClick(e);
        onClick(e);
      }
    },
    [eventContext, onClick],
  );
  const content = (
    <div
      {...addDebugOutlineIfEnabled()}
      css={[styles['container'], dangerouslyAppendEmotionCSS]}
      tabIndex={disabled ? -1 : tabIndex}
      onClick={onClickWrapper}
      onKeyDown={(e) => {
        if (!onClick || disabled) {
          return;
        }

        if (e.key === 'Enter' || e.key === ' ') {
          e.preventDefault();
          onClickWrapper(e as unknown as React.MouseEvent<HTMLDivElement>);
        }
      }}
      role={role}
      aria-disabled={disabled}
      aria-pressed={selected}
      {...props}
      ref={previewCardRef}
    >
      {image && <div css={styles['image']}>{image}</div>}
      <div css={styles['header']}>
        {icon && <div>{icon}</div>}
        <div css={styles['titleWrapper']}>
          {title && <div css={styles['title']}>{title}</div>}
          {subtitle && <div css={styles['subTitle']}>{subtitle}</div>}
        </div>
        {titleActions && <div>{titleActions}</div>}
      </div>
      {children && <div css={styles['childrenWrapper']}>{children}</div>}
      {showFooter && (
        <div css={styles['footer']}>
          <div css={styles['action']}>{startActions}</div>
          <div css={styles['action']}>{endActions}</div>
        </div>
      )}
    </div>
  );

  if (href) {
    return (
      <a href={href} target={target} style={{ textDecoration: 'none' }}>
        {content}
      </a>
    );
  }

  return content;
};
const usePreviewCardStyles = ({
  onClick,
  size,
  disabled,
  fullBleedImage,
}: Pick<PreviewCardProps, 'onClick' | 'size' | 'disabled' | 'fullBleedImage'>): Record<string, CSSObject> => {
  const { theme } = useDesignSystemTheme();
  const { useNewShadows } = useDesignSystemSafexFlags();
  const isInteractive = onClick !== undefined;
  const paddingSize = size === 'large' ? theme.spacing.lg : theme.spacing.md;
  return {
    container: {
      overflow: 'hidden',
      borderRadius: theme.borders.borderRadiusMd,
      border: `1px solid ${theme.colors.border}`,
      padding: paddingSize,
      color: theme.colors.textSecondary,
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'space-between',
      gap: size === 'large' ? theme.spacing.md : theme.spacing.sm,
      ...(useNewShadows
        ? {
            boxShadow: theme.shadows.sm,
          }
        : {}),
      cursor: isInteractive ? 'pointer' : 'default',
      ...(isInteractive && {
        '&[aria-disabled="true"]': {
          pointerEvents: 'none',
          backgroundColor: theme.colors.actionDisabledBackground,
          borderColor: theme.colors.actionDisabledBorder,
          color: theme.colors.actionDisabledText,
        },
        '&:hover, &:focus-within': {
          boxShadow: useNewShadows ? theme.shadows.md : theme.general.shadowLow,
        },
        '&:active': {
          background: theme.colors.actionTertiaryBackgroundPress,
          borderColor: theme.colors.actionDefaultBorderHover,
          boxShadow: useNewShadows ? theme.shadows.md : theme.general.shadowLow,
        },
        '&:focus, &[aria-pressed="true"]': {
          outlineColor: theme.colors.actionDefaultBorderFocus,
          outlineWidth: 2,
          outlineOffset: -2,
          outlineStyle: 'solid',
          boxShadow: useNewShadows ? theme.shadows.md : theme.general.shadowLow,
          borderColor: theme.colors.actionDefaultBorderHover,
        },
        '&:active:not(:focus):not(:focus-within)': {
          background: 'transparent',
          borderColor: theme.colors.border,
        },
      }),
    },
    image: {
      margin: fullBleedImage ? `-${paddingSize}px -${paddingSize}px 0` : 0,
      '& > *': {
        borderRadius: fullBleedImage ? 0 : theme.borders.borderRadiusSm,
      },
    },
    header: {
      display: 'flex',
      alignItems: 'center',
      gap: theme.spacing.sm,
    },
    title: {
      fontWeight: theme.typography.typographyBoldFontWeight,
      color: disabled ? theme.colors.actionDisabledText : theme.colors.textPrimary,
      lineHeight: theme.typography.lineHeightSm,
    },
    subTitle: {
      lineHeight: theme.typography.lineHeightSm,
    },
    titleWrapper: {
      flexGrow: 1,
      overflow: 'hidden',
    },
    childrenWrapper: {
      flexGrow: 1,
    },
    footer: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      flexWrap: 'wrap',
    },
    action: {
      overflow: 'hidden',
      // to ensure focus ring is rendered
      margin: theme.spacing.md * -1,
      padding: theme.spacing.md,
    },
  };
};
