import {
  DesignSystemEventProvider,
  type DesignSystemEventProviderCallback,
  type DesignSystemEventProviderCallbackParams,
} from './DesignSystemEventProvider';

export const setupDesignSystemEventProviderForTesting = (
  eventCallback: DesignSystemEventProviderCallback,
): {
  DesignSystemEventProviderForTest: ({ children }: { children: React.ReactNode }) => JSX.Element;
} => {
  const eventCallbackWrapper = (arg: DesignSystemEventProviderCallbackParams) => {
    if (arg?.componentViewId) {
      const { componentViewId, ...rest } = arg; // strip componentViewId out of jest call args
      eventCallback(rest);
      return;
    }
    eventCallback(arg);
  };
  return {
    DesignSystemEventProviderForTest: ({ children }: { children: React.ReactNode }) => (
      <DesignSystemEventProvider callback={eventCallbackWrapper}>{children}</DesignSystemEventProvider>
    ),
  };
};
